import Log from './Log'
import { de } from 'date-fns/locale'
export const locales = { de }


/**
 * Internationalization
 * Based on https://raw.githubusercontent.com/ioBroker/adapter-react/master/src/i18n.js.
 * Copyright 2018-2019 bluefox <dogafox@gmaiLog.com>
 *
 * MIT License
 *
 *
 */
export default class i18n {

	/*
	 * holds the translations
	 */
	static translations = {};

	/*
	 * default language
	 */
	static language = (navigator.language || navigator.userLanguage || 'en-EN');

	/**
	 * Override default language.
	 *
	 * @param	{String}	language
	 * @return	{Boolean}
	 */
	static setLanguage(language) {
		if (language) {
			i18n.language = language;
			Log._('i18n', 'Set language to ' + language + '.', 'debug');
			return true;
		}
		
		return false;
	}
	
	/**
	 * Set a translation for language.
	 *
	 * @param	{String}	language
	 * @param	{Object}	translation
	 * @return	{Boolean}
	 */
	static setTranslation(language, translation) {
		
		// load translation file
		if (typeof translation === 'function') {
			let file;
			
			try {
				file = translation();
				translation = file.default;
			}
			catch(err) {
				console.warn('Could not load translation file!');
				return false;
			}
		}
		
		// set translation
		if (language && translation) {
			i18n.translations[language] = { ...i18n.translations[language] || {}, ...translation };
			Log._('i18n', 'Set translations for language ' + language + '.', 'debug');
			return true;
		}
		
		return false;
	}
	
	/**
	 * Get current set language.
	 *
	 * @param	void
	 * @return	{String}
	 */
	static getLanguage(isoLong = false) {
		return i18n.language.substr(0, isoLong ? 5 : 2);
	}
	
	/**
	 * Get current set language.
	 *
	 * @param	{String|Object}		word
	 * @param	{Object}			[options={}]
	 * @param	{Boolean}			[options.ucFirst=false]
	 * @param	{Object}			[placeholders={}]
	 * @param	{String}			[language=i18n.language]
	 * @return	{String}
	 */
	static t(word = '', options = {}, placeholders = {}, language = i18n.getLanguage()) {
		
		// if object is provided, it might be translated already
		if (typeof word === 'object' && word[language] !== undefined) {
			word = word[language];
		}
		
		// if array, then multiple words are given
		else if (Array.isArray(word)) {
			word = word.map(w => i18n.t(w, options, placeholders, language)).join('');
		}
		
		// otherwise, treat as string
		else if (word) {
			word = word.toString();
			if (i18n.translations[language]) {
				
				// check if word is concatenated
				if (word.indexOf('#') > -1) {
					
					let parts = word.split('#');
					while (parts.length > 0 && !i18n.translations[language][parts.join('#')]) {
						parts.shift();
					}
					
					word = parts.join('#');
				}
				
				if (i18n.translations[language][word]) {
					word = i18n.translations[language][word];
				}
				else if (i18n.translations[language][word.toLowerCase()]) {
					word = i18n.translations[language][word.toLowerCase()];
				}
				else {
					Log._('i18n', 'Word "' + word + '" not found in language ' + language + '!', 'debug');
				}
			}
			else {
				Log._('i18n', 'Language ' + language + ' not loaded for word ' + word + '!', 'error');
			}
		}
		
		// replace placeholders
		for (let key in placeholders) {
			word = word.replace(RegExp('%' + key + '%', 'g'), placeholders[key]);
		}
		
		// options
		if (options.ucFirst) {
			word = word.charAt(0).toUpperCase() + word.slice(1);
		}
		
		return word;
	}
}
