import i18n from '../controller/i18n'


export const Functions = {
	defaults: {
		'en': 'Default settings',
		'de': 'Standard-Einstellungen',
	},
	
	blind: {
		'en': 'Shutters / Blinds',
		'de': 'Rollos / Jalousien',
	},
	door: {
		'en': 'Doors',
		'de': 'Tren',
	},
	heating: {
		'en': 'Heating',
		'de': 'Heizung',
	},
	light: {
		'en': 'lights',
		'de': 'Lichter',
	},
	location: {
		'en': 'locations',
		'de': 'Orte',
	},
	user: {
		'en': 'users',
		'de': 'Benutzer',
	},
	window: {
		'en': 'windows',
		'de': 'Fenster',
	}
}


let _ = {}

Object.keys(Functions).forEach(func => {
	
	if (func === 'defaults') {
		_[func] = require('./' + func).default;
	}
	else {
		_[func] = require('./' + func + '/' + func).default;
	}
	
	// load language file
	try {
		i18n.setTranslation(i18n.getLanguage(), require('./' + func + '/i18n/' + i18n.getLanguage()).default);
	}
	catch(err) {}
});

export default _;
