import i18n from '../controller/i18n'


export const Modules = {
	'AdapterStatus': {
		'de': 'Zeigt alle installierten Adapter mit ihren Instanzen und dem jeweiligen Status (active & alive, active & not connected, inactive) an.'
	},
	'Chart': {
		'de': 'Stellt historische Werte (z.B. aus ioBroker.history) grafisch als Chart mittels Chart.js dar.'
	},
	'DateTime': {
		'de': 'Das Modul dient der Anzeige des aktuellen Datums und Zeit.'
	},
	'iFrame': {
		'de': 'Zeigt eine externe URL an.'
	},
	'Map': {
		'de': 'Zeigt eine Karte von OpenStreetMap.'
	},
	/*'Multimedia': {
		'de': 'Zeigt ein Multimedia-Player an (z. B. Spotify)'
	},*/
	'StateList': {
		'de': 'Zeigt eine Liste von States an.'
	},
	'StateListHorizontal': {
		'de': 'Zeigt eine Liste von States horizontal an'
	},
	/*'Weather': {
		'de': 'Zeigt die Wetter-Vorschau an.'
	}*/
}


let _ = {}
let Settings = {}
let Configurations = {}

Object.keys(Modules).forEach(module => {
	_[module] = require('./' + module + '/' + module).default;
	
	// load settings
	Settings[module] = require('./' + module + '/' + module + '.settings').default;
	
	// load configurations
	Configurations[module] = require('./' + module + '/' + module + '.config').default;
	
	// load language file
	try {
		i18n.setTranslation(i18n.getLanguage(), require('./' + module + '/i18n/' + i18n.getLanguage()).default);
	}
	catch(err) {}
});

export const ModuleSettings = Settings;
export const ModuleConfigurations = Configurations;
export default _;
